import userChatBottom from '../../components/user-chat/user-chat-bottom.vue';
import time from "../../common/time.js";
import userChatList from '../../components/user-chat-list/user-chat-list.vue';
export default {
	components:{
		userChatBottom,
		userChatList
	},
	data() {
		return {
			scrollTop:0,
			style:{
				contentH:0,
				itemH:0
			},
			list:[]
		}
	},
	onLoad() {
		this.getdata();
		this.initdata();
	},
	methods: {
		// 获取聊天数据
		getdata(){
			// 从服务器获取到的数据
			let arr=[
				{
					isme:false,
					userpic:"../../static/demo/userpic/11.jpg",
					type:"text",
					data:"哈哈哈",
					time:"1555146412"
				},
				{
					isme:true,
					userpic:"../../static/demo/userpic/10.jpg",
					type:"img",
					data:"../../static/demo/3.jpg",
					time:"1555146414",
				},
			];
			for (let i = 0; i < arr.length; i++) {
				arr[i].gstime=time.gettime.getChatTime(arr[i].time,i>0?arr[i-1].time:0);
			}
			this.list=arr;
		},
		submit(data){
			console.log(data)
			// 构建数据
			let now=new Date().getTime();
			let obj={
				isme:true,
				userpic:"../../static/demo/userpic/10.jpg",
				type:"text",
				data:data,
				time:now,
				gstime:time.gettime.getChatTime(now,this.list[this.list.length-1].time)
			};
			this.list.push(obj);
			this.scrollToBottom()
		},
		initdata(){
			try {
				const res = uni.getSystemInfoSync();
				this.style.contentH=res.windowHeight - uni.upx2px(120);
			} catch (e) { }
		},
		scrollToBottom(){
			const query = uni.createSelectorQuery();
			query.selectAll('.user-chat-item').boundingClientRect(data => {
			  console.log("得到布局位置信息" + JSON.stringify(data));
			  console.log("节点离页面顶部的距离为" + data.top);
			}).exec();
		}
	}
}
